@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  font-size: 24px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 60vmin;
  aspect-ratio: 1/1;
  pointer-events: none;
  border-radius: 50%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card {
  display: flex;
  gap: 0.5em;
  border: aquamarine 2px solid;
  padding: 0.25em;
  border-radius: 0.25em;
  font-size: 1em;
  max-width: 600px;
  background-image: linear-gradient(magenta,red);
}

img {
  border-radius: 0.15em;
  filter: blur(2px);
}